#wrap {
	position: relative;	
}

.container {
	max-width: $pc-wide;
	margin: 0 auto;
	padding-left: 10px;
    padding-right: 10px;
}

.hgroup {
	@include breakpoint($smd) {
		padding: 0 10px;
		box-sizing: border-box;
	}
}

body.gnbOver {
	#header {
		position: fixed !important;
	}
	.navigation {
		margin-top: 148px;
		@include breakpoint($sm) {
			margin-top: 100px;
		}
	}

}


#header {
