			min-height: 60px;
		}
	}
	.globalHeader {	
		// display: none;	
		display: block;
		min-height: 34px;
		ul {
			max-width: calc(1920px - 192px);
			display: flex;
			flex-direction: row;
			flex-grow: 1;
			// border-bottom: 1px solid rgba(255, 255, 255, 0.2);
			// padding: 4px 0;
			gap: 0 2px;
			@include breakpoint($smd) {
				display: none;
			}
			li {
				& + li {
					&::before {
						content: "";
						display: inline-block;
						vertical-align: middle;
						width: 1px;
						height: 14px;
						background-color: #e5e5e5;
					}
				}
			}
			a {
				//color: #fff;
				color: #005596;
				display: inline-block;
				font-size: rem(15);
				font-weight: 500;
				padding: 14px 8px;
				@include breakpoint($smd) {
					font-size: rem(13);
				}
				&:hover {
					text-decoration: underline;
				}
			}
		}
		.m-globalHeader {
			display: none;
			@include breakpoint($smd) {
				display: block;
			}
			button {
				width: 156px;
				margin: 4px 0;
				height: 30px;
				font-size: rem(12);
				border: 1px solid #D9D9D9;
				line-height: 20px;
				/* height: 40px; */
				outline: none;
				border-radius: 5px;
				-webkit-appearance: none;
				padding: 4px 30px 4px 10px !important;
				background: #fff url(/static/portal/img/common/select-arr.png) calc(100% - 10px) center no-repeat;
				text-align: left;
			}
			.site_go {
				position: relative;
				ul.active {
					display: block;
					position: absolute;
					top: 35px;
					left: 0;
					z-index: 9;
					width: 100%;
					padding: 10px;
					border-radius: 8px;
					border: 1px solid #E5E5E5;
					background-color: #fff;
					li {
						&::before {
							display: none;
						}
					}
					a {
						color: #666;
						display: inline-block;
						font-weight: 400;
						padding: 4px 0;
						line-height: 1.2;
						font-size: rem(14);
						text-align: left;
					}
				}
			}
		}
	}
	.logo {
		position: relative;
		display: inline-flex;
		align-items: center;
		gap: 0 26px;
		vertical-align: middle;
		z-index: 2;
		@include breakpoint($smd){
			height: 60px;
		}
		a {
			display: block;
			background: url(/static/portal/img/common/logo.png) 50% no-repeat;
			width: 127px;
			height: 60px;
			@include breakpoint($sm){
