#header {
	//
	.userInfoOpBxSet {
		padding: 3px 0;
		position: relative;
		@include breakpoint($sm) {
			padding: 4px 0;
			margin-right: -4px;
		}
		.userInfoOpener {
			border-radius: 30px;
			background: #005596;
			display: inline-block;
			padding: 1px 10px !important;
			color: #fff;
			font-size: 14px;
			@include breakpoint($sm) {
				font-size: rem(13);
			}
		}
		.userInfoOpBx {
			display: none;
			width: 280px;
			position: absolute;
			top: 42px;
			left: -138%;
			margin-left: -40px;
			border: 1px solid #3B63C4;
			border-radius: 5px;
			padding: 24px;
			box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
			background-color: #fff;
			z-index: 99;
			&.on {
				display: block;
			}
			.userInfoSnsBx {
				display: flex;
				//justify-content: space-between;
				align-items: center;
				.infoTxts {
					margin-left: 14px;
					line-height: 1.2;
					strong {
						font-weight: 500;
					}
				}
				i {
					text-align: center;
					display: inline-block;
					width: 64px;
					height: 64px;
					line-height: 64px;
					border-radius: 50%;
					background-color: #B1C1E7;
					&::before {
						@include font-ico('\e7fd');
						font-size: 35px;
						color: #fff;
						margin-top: 14px;
					}
				}
			}
			.myLogoutBx {
				margin-top: 15px;
				display: flex;
				justify-content: space-between;
				a {
					text-align: center;
					width: calc(50% - 2px);
					display: block;
					color: #fff;
					font-size: 14px;
					font-weight: 300;
					border-radius: 4px;
					padding: 4px;
					&.mypg {
						background-color: $color-point;
						&::before {
							content: "";
							display: inline-block;
							vertical-align: middle;
							width: 21px;
							height: 20px;
							background: url(/static/portal/img/ico/ico-person_edit.png);
							margin-right: 4px;
						}
					}
					&.logout {
						background-color: #4D4D4D;
						&::before {
							@include font-ico-outline('\e9ba');
							font-size: 18px;
							margin-right: 4px;
						}
					}
				}
			}
		}
	}

	.m-g-info {
		.global .userInfoOpBxSet {
			padding: 0;
			.userInfoOpener {
				font-size: rem(14);
				padding: 2px 8px !important;
			}
			.userInfoOpBx {
				@include breakpoint($smd) {
					top: 42px;
				}
			}
		}
	}
	.g-info {
		.global .userInfoOpBxSet {
			.userInfoOpBx {
				@include breakpoint($sm) {
					left: -166%;
   					top: 35px;
				}
			}
		}
	}
}


