#header.family {
	position: relative;
	background: #fff;
	//border-bottom: 1px solid #E5E5E5;
	backdrop-filter: blur(4px);
	@include breakpoint($smd){
		position: relative !important;
		//border-bottom: 1px solid #E5E5E5;
	}
	.globalHeader {
		> .container {
			border-bottom: 1px solid #e5e5e5;
		}
	}
	.logo {
		> a {
			display: inline-flex;
			width: auto;
			align-items: center;
			background: transparent;
			gap: 0 32px;
			@include breakpoint($sm){ 
				gap: 0 24px;
			}
		}
		&-portal {
			display: inline-block;
			width: 136px;
			height:  46px;
			@include background-image-retina("/static/museum/img/common/logo-blue", png, auto, 100%);
			background-size: contain;
			@include breakpoint($sm){
				width: 100px;
				height: 32.5px;
			}
		}
		&-museum {
			display: inline-block;
			position: relative;
			width: 83px;
			height:  37px;
			padding-left: 24px;
			@include background-image-retina("/static/museum/img/common/logo-museum-bl", png, auto, 100%);
			background-size: contain;
			background-position: center center;
			@include breakpoint($sm){
				width: 66.5px;
				height: 24.5px;
			}
			&::before {
				content: '';
				position: absolute;
				display: inline-block;
				left: -16px;
				width: 2px;
				height: 34px;
				background: #005596;
				opacity: .3;
				@include breakpoint($sm){ 
					left: -12px;
					height: 24px;
				}
			}
		}
	}
	
}
#header.main {
	position: fixed !important;
	top: 0;
	background: rgba(0,0,0,0.6);
	.globalHeader {
		> .container {
			border-bottom: 2px solid rgba(255,255,255,0.1);
			> ul li  {
				& + li::before {
					background: rgba(255,255,255,0.3);
				}
				a {
					// border: 1px solid rgba(255, 255, 255, 0.40);
					// background: rgba(0, 0, 0, 0.20);
					// border-radius: 4px;
					color: #fff;
				}
			}
		}
	}
	.g-info .global ul li {
		> a {
			color: #fff;
		}
		& + li::before {
			background: rgba(255,255,255,0.3);
		}
	}
	.gnb {
		> ul > li {
			&.active > a {
				span {
					color: #005596;
					&::before {
						background: #005596;
					}
				}
			}
			> a {
				color: #fff;
			}
		}
	
		.gnb-btn-wrap > .search {
			background: #3B63C4;	
		}
		.gnb-btn-wrap .m-g-info .global ul a  {
			color: #fff;
		}
		.all-nav-toggle::before {
			color: #fff;
		}
	} 


	.logo-portal {
		background-image: url(/static/museum/img/common/logo-portal.png);
	}
	.logo-museum {
		background-image: url(/static/museum/img/common/logo-museum.png);
		&::before {
			background: #fff;
			opacity: 0.3;
		}
	}
}

.gnb-stiky #header.main,
#header.main.active {
	background: #fff;
	//border-bottom: 1px solid #E5E5E5;
