.total-nav {
	position: fixed;
	align-items: center;
	justify-content: center;
	top: 0;
	left: 0;
	padding: 0 10px;
	width: 100%;
	min-height: 100vh;
	background: rgba(0,0,0,0.7);
	// backdrop-filter:blur(10px);
	z-index: 100;
	//overflow: auto !important;
	display: none;
	&.in::before {
		display: none !important;
	}
	.modal-dialog {
		overflow: hidden;
		max-width: 1600px;
		max-height: calc(100vh - 160px);
		@include breakpoint($sm) {
			max-height: calc(100vh - 40px);
		}
	}
	.modal-dialog,
	.modal-content {
		width: 100%;
		// min-width: 100vh;
		// min-height: 100vh;
		height: 100%;
		background-color: #fff;
		border: 0;
		border-radius: 0;
	}
	.modal-header {
		background: #F6F6F6;
		padding: 24px 38px;
		border-radius: 0;
		.modal-title {
			display: flex;
			align-items: center;
			gap: 0 24px;
			color: #005596;
		}
		.totalnav-logo {
			display: flex;
			position: relative;
			width: 160px;
			height: 52px;
			background: url(/static/portal/img/common/logo-totalNav.png) center center no-repeat;
			margin-right: 24px;
			&::before {
				content: "";
				position: absolute;
				display: block;
				width: 1px;
				height: 36px;
				background: #005596;
				top: 50%;
				right: -24px;
				transform: translateY(-50%);
			}
		}
		.close {
			width: 48px;
			height: 48px;
			&::before {
				color: #000;
			}
			i {
				display: none;
			}
		}
		.keyword-set {
			width: 320px;
			margin-right: 88px;
			display: flex;
			align-items: center;
			background-color: #fff;
			border: 1px solid #e5e5e5;
			border-radius: 4px;
			overflow: hidden;
			@include breakpoint($sm){
				width: 100%;
				margin-top: 4px;
				//flex-basis: calc(100% - 100px - 2px);
			}
			.keyword {
				width: calc(100% - 24px - 8px);	
				input[type=text] {
					width: 100%;
					border: 0;
					height: 48px;
					padding: 10px 16px;
					color: #1a1a1a;
					@include breakpoint($sm){
						padding: 5px 16px;
						height: 40px;
					}
				}
			}		
			.search {
				width: 48px;
				height: 48px;
				text-align: center;
				// @include breakpoint($sm){
				// 	width: 40px;
				// 	height: 40px;
				// }
				&::before {
					@include font-ico-outline('\e8b6');
					font-size: 26px;
					color: #222;
				}
			}
		}
	}
	.modal-body {
		height: calc(100% - 100px);
		padding: 44px 48px;
		overflow-y: auto;
		.container {
			max-width: 1680px;
			align-items: stretch !important;
			justify-content: flex-start !important;
			flex-wrap: wrap;
			gap: 0 16px;
		}
		.highlight {
			display: inline-block;
			padding: 0 2px;
			margin-right: 2px;
			background: #FFE100;
		}
		.dp1 {
			flex: 1;
			> li {
				font-size: rem(26);
				font-weight: 500;
				> a {
					color: #333333;
					@include breakpoint($sm) {
						display: none;
					}
				}
				> a.m-dp1 {
					display: none;
					@include breakpoint($sm) {
						display: inline-block;
					}
				}
			}
		}
		.dp2 {
			margin-top: 16px;
			> li {
				font-size: rem(18);
				padding: 6px 0;
				> a {
					color: #26447D;
				}
			}
		}
		.dp3 {
			margin-top: 6px;
			> li {
				position: relative;
				font-size: rem(16);
				padding: 4px 0 4px 4px;
				&::before {
					content: "";
					display: inline-block;
					position: absolute;
					top: 16px;
					left: 4px;
					width: 4px;
					height: 4px;
					border-radius: 50%;
					background: #3E7CC8;
				}
				> a {
					display: inline-flex;
					align-items: flex-start;
					padding-left: 16px;
					color: #4D4D4D;
				}
				&.dropdown {
					padding: 4px 28px 4px 4px;
					> a {
						position: relative;
						// display: inline-flex;
						align-items: center;
						color: #4d4d4d;
						i {
							position: absolute;
							top: 4px;
							right: -25px;
