.admin-set {	
	clear: both;
	width: 100%;
	padding-top: 60px;
	@include breakpoint($smd){
		padding-top: 20px;
	}
	.satisfact {
		position: relative;		
		display: flex;
		border-radius: 8px;
		border: 1px solid #BFBFBF;
		@include breakpoint($smd){
			display: block;
		}
		.title {
			text-align: center;
			width: 242px;
			background-color: #FAFAFA;
			border-radius: 8px 0 0 8px;
			height: 64px;
			line-height: 64px;
			margin-bottom: 0;
			@include breakpoint($smd){
				width: 100%;
				text-align: left;
				height: 32px;
				line-height: 32px;
				padding: 0 18px;
				border-radius: 8px 8px 0 0;
				border-bottom: 1px solid #BFBFBF;
			}
			&::before {
				content: "";
				display: inline-block;
				vertical-align: text-bottom;
				width: 22px;
				height: 22px;
				background: url(/static/portal/img/common/ico_edit_square.png) 50% no-repeat;
				margin-right: 8px;
				margin-bottom: 1px;
				@include breakpoint($sm){
					width: 15px;
					height: 15px;
					background-size: 15px;
				}
			}
		}
		.radio-group {
			display: flex;
			align-items: center;
			padding: 20px 30px;
			@include breakpoint($smd){
				display: block;
				padding: 10px 3px;
			}
			label {
				@include breakpoint($smd){
					&:first-child {
						margin-left: 14px;
					}
				}
				& + label {
					margin-left: 14px;
				}
			}
			input[type=radio] {	
				& + i + span {
					color: #4D4D4D;
					margin-left: 10px;
				}
				&:checked + i {
					& + span {
						font-weight: 500;
						color: $point;
						text-decoration: underline;
					}
				}
			}
			@include breakpoint($smd){
				label {
					width: 100%;
					margin: 3px 0;
				}
			}
		}
		button {
			position: absolute;
			top: 50%;
			right: 20px;
			margin-top: -16px;
			color: #fff;
			background-color: $point;
			border-radius: 4px;
			padding: 4px 14px;
			@include breakpoint($smd){
				top: auto;
				bottom: 10px;
			}
			&::before {
				@include font-ico-outline('\e5ca');
				font-size: 18px;
				color: #fff;
				margin-right: 5px;
				margin-bottom: 2px;
			}
			&:focus {
				outline: dashed 1px $black;
				outline-offset: 2px;
			}
		}
	}
	.info {
		margin-top: 10px;
		color: $black;
		dl {
			display: flex;
			justify-content: right;
			@include breakpoint($smd){
				justify-content: left;
			}
			&::before {
				content: "";
				display: inline-block;
				vertical-align: middle;
				width: 15px;
				height: 15px;
				background: url(/static/portal/img/ico/ico-call.png);
				margin: 5px 10px 0 0;
			}
			dt {
				&::after {
					content: "";
					display: inline-block;
					vertical-align: middle;
					width: 1px;
					height: 14px;
					background-color: $black;
					margin: 0 14px;
				}
			}
			dd {
				&:last-child {
					margin-left: 12px;
				}
			}
		}
	}
}

