	.more {
		width: 100%;
		text-align: center;
		margin-top: 14px;
		a {
			color: #3B63C4;
			&::before {
				@include font-ico-outline('\e5cf');
				font-size: 20px;
				color: #3B63C4;
				margin-right: 4px;
			}
		}
