.reference {
	clear: both;
	margin-top: 30px;
	padding: 50px 120px;
	background-color: #E5ECFF;
	@include breakpoint($smd){
		padding: 16px;
		margin: 30px -10px 0;
	}
	&-data {
		@include reference();
		dl {
			dt {
				font-weight: 500;
				color: #222;
				margin-bottom: 20px;
				&::before {
					content: "";
					display: inline-block;
					vertical-align: middle;
					width: 20px;
					height: 20px;
					background: url(/static/portal/img/ico/ico-export_notes.svg);
					margin: 0 6px 0 0;
				}
			}
			dd {
				@include breakpoint($sm){
					margin-right: -10px;
				}
				ul {
					display: flex;
					flex-wrap: wrap;
					margin: -12px -12px 0;
					@include breakpoint($smd){
						margin: -12px 0 0 -5px;
					}
					li {
						width: calc(100% / 4);
						padding: 12px;
						@include breakpoint($smd){
							width: 100%;
							padding: 3px 5px;
						}
					}
				}
				a {
					display: block;
					color: $black;
					background-color: #fff;
					border-radius: 8px;
					padding: 15px 15px;		
					@include breakpoint($smd){
						padding: 8px 12px;
						font-size: 12px;
					}			
					i {
						&::before {
							content: "";
							display: inline-block;
							vertical-align: middle;
							width: 24px;
							height: 24px;
							@include breakpoint($smd){
								width: 18px;
								height: 18px;
								background-size: cover;
							}
						}
						&.m {
							&::before {
								background-image: url(/static/portal/img/ico/ico_LinkedData1.svg);
							}
						}
						&.c {
							&::before {
								background-image: url(/static/portal/img/ico/ico_LinkedData2.svg);
							}
						}
						&.h {
							&::before {
								background-image: url(/static/portal/img/ico/ico_LinkedData3.svg);
							}
						}
					}
				}
			}
		}
		.lnb-nav & {
			border-top: 1px solid #E5E5E5;
			padding-top: 26px;
			margin-top: 28px;
			dl {
				dd {
					ul {
						margin: 0;
						li {
							width: 100%;
							padding: 0;
							a {
								width: 100%;
								display: block;
								padding: 7px 8px;
								background-color: #F2F6FA;
								border-radius: 8px;
								@include ellipsis;
								i {
									margin-right: 5px;
								}
							}
						}
					}
				}
			}
		}
	}
	&-news {
		margin-top: 20px;
		padding-top: 30px;
		border-top: 1px solid #DCE2F1;
		@include reference();		
		dl {
			dt {
				&::before {	
					width: 21px;
					height: 19px;	
					background: url(/static/portal/img/ico/ico-data.svg);
					margin: 0 6px 0 0;					
				}
			}
			dd {
				@include breakpoint($smd){
					width: calc(100%  + 16px);
					overflow-x: auto;
					overflow-y: hidden;
				}
				ul {
					@include breakpoint($smd){
						width: max-content;
					}
					display: flex;
					flex-wrap: wrap;
					flex-direction: row;
					margin: 0 -12px;
					li {
						width: calc(100% / 4);
						padding: 12px;
						@include breakpoint($smd){
							width: 270px;
						}
						a {
							display: block;
							span {
								display: block;
								&.t1 {
									color: $black;
									margin-bottom: 8px;
								}
								&.t2 {
									color: #666;
									font-size: rem(14);
								}
								&.t {
									color: #3B63C4;
									font-size: rem(14);
									margin-bottom: 4px;
								}
								&.title {
									max-height: 72px;
									@include line-clamp(3);
								}
								&.date {
									color: #666;
									font-size: rem(14);
									margin-top: 10px;
								}
							}
						}
					}
				}
			}
		}
	}
	&-media {
		border-top: 1px solid #DCE2F1;
		@include reference();
		margin-top: 25px;
		padding-top: 30px;				
		dl {
			dt {
				&::before {
					@include font-ico-outline('\e8a7');
					font-size: 22px;
					margin: 0 8px 4px 0;
				}
			}
			dd {
				@include breakpoint($smd){
					width: calc(100%  + 16px);
					overflow-x: auto;
					overflow-y: hidden;
				}			
				ul {
					padding-top: 12px;
					display: flex;
					flex-wrap: wrap;
					margin: -12px -12px 0;
					@include breakpoint($smd){
						width: max-content;
					}					
					li {
						width: calc(100% / 4);
						padding: 12px;
						@include breakpoint($smd){
							width: 270px;
						}
						a {							
							display: block;
							span {
								display: block;
								&.img {
									position: relative;
									padding-top: 62%;
									background-position: 50%;
									background-size: cover;
									background-repeat: no-repeat;
									border-radius: 8px;
									margin-bottom: 15px;
									&::before {
										content: "";
										display: inline-block;
										width: 40px;
										height: 40px;
										background-image: url(/static/portal/img/ico/ico-play.svg);
										position: absolute;
										top: 50%;
										left: 50%;
										margin-left: -20px;
										margin-top: -20px;
									}
								}
								&.t {
									color: #3B63C4;
									font-size: rem(14);
								}
								&.tit {
									color: $black;
									@include line-clamp(2);
									height: 48px;
									margin: 5px 0 10px;
								}
								&.date {
									color: #666;
								}
							}
						}
					}
				}
			}
		}
	}
}

