			background-color: #fff;
			margin: 0 30px;
			width: calc(100% - 60px);
			.autoList {
				max-height: 180px;
				overflow-y: auto;
				li {
					a {
						font-size: 16px;
						display: block;
						padding: 3px 8px;
						&:hover {
							background-color: #EBF3F7;
						}
					}
				}
			}
			.bottom {
				display: flex;
				justify-content: right;
				margin-top: 15px;
				background-color: #FAFAFA;
				padding: 8px;
				.btnClose {
					font-size: 14px;
					color: #6D6E72;
					&::before {
						@include font-ico('\e5c9');
						font-size: 21px;
						color: #6D6E72;
						margin-right: 3px;
					}
				}
			}
			.tt-suggestion {
				cursor: pointer !important;
				&:hover {
					background-color: #ebf3f7 !important;
				}
			}
			.tt-highlight {
				color: #005596 !important;
			}
		}
	}
	.search_close {
		position: absolute;
		top: 25px;
		right: 25px;
		&::before {
			@include font-ico-outline('\e5cd');
			font-size: 40px;
			color: $black;
		}
	}
}

/*  */
.page-toolbar {
	position: relative;
	display: flex;
	align-items: center;
	@include breakpoint($smd){
		display: none;
	}
	.sns-toggle {
		width: 60px;
		height: 60px;
		border: 1px solid #666;
		border-radius: 50%;
		&::before {
			@include font-ico('\e80d');
			font-size: 26px;
			color: #666;
		}
	}
	.sns-wrap {	
		display: none;		
		&.active {
			display: block;
			position: absolute;
			width: 60px;
			// height: 236px;
			top: -1px;
			left: -1px;
			z-index: 9;
			.sns-wrap__set {
				display: flex;
				width: 62px;
				align-items: center;
				flex-direction: column;
				border-radius: 8px;
				border: 2px solid #1a1a1a;
				background-color: #fff;
				padding: 20px 10px;
				gap: 16px 0;
				button {
					display: flex;
					align-items: center;
					justify-content: center;
					margin: 0 auto;
					&::before {
						content: "";
						display: inline-block;
						width: 36px;
						height: 36px;
						background-position: 50%;
						background-repeat: no-repeat;
					}
					&.close {
						height: 24px;
						&::before {
							@include font-ico-outline('\e5cd');
							width: 24px;
    						height: 24px;
							font-size: 24px;
						}
					}
				}
			}
		}
	}
	.page-print {	
		width: 60px;
		height: 60px;
		background: url(/static/portal/img/ico/ico-print.svg) 50% no-repeat;
		margin-left: 6px;
		margin-right: 10px;
	}
	.page-rss {	
		width: 60px;
		height: 60px;
		&::before {
			@include font-ico-outline('\e0e5');
			font-size: 40px;
			color: #666;
		}
	}
}

