				background-color: #CBDEF4;
				div {
					padding: 0; 
					h1 {
						text-align: center;
						line-height: 1.5;
					}
				}
			}
		}
		.bd-view {
			.subject {
				height: auto;
				font-size: rem(20) !important;
				overflow:visible;
				text-overflow: initial;
				display: block;
				-webkit-line-clamp: initial;
				-webkit-box-orient: initial;
				margin-bottom: 0 !important;
				margin-top: 54px !important;
				padding-top: 16px;
				border-top: 1px solid #e5e5e5;
				& + .dataInfoSet {
					display: flex;
					align-items: flex-start;
					margin: 16px 0;
					.dataInfo {
						top: -38px;
						font-size: rem(14);
					}
					.keyword {
						position: relative;
						order: 3;
						width: 100%;
						margin-top: 16px;
						padding: 16px 6px;
						background: #fafafa;
						&::before {
							content: "";
							display: block;
							position: absolute;
							top: 0;
							left: -10px;
							width: 10px;
							height: 100%;
							background: #fafafa;
						}
						&::after {
							content: "";
							display: block;
							position: absolute;
							top: 0;
							right: -10px;
							width: 10px;
							height: 100%;
							background: #fafafa;
						}
						&.noKeyword {
							padding: 0;
							background: #fff;
							&::before,
							&::after {
								display: none;
							}
						}
					}
					.type2 {
						align-items: flex-start;
						order: 2;
						font-size: rem(14);
					}
				}
			}
		}
	}
}

