//
.loading {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 4;
	width: 100%;
	height: 600px;
	background: #fff;
	@include breakpoint($sm) {
		height: 400px;
	}
	// @include breakpoint($smd) {
	// 	height: 600px;
	// }
	.row {
		height: 100%;
		align-items: center;
		//justify-content: center;
		flex-direction: column;
		padding: 100px 0;
		@include breakpoint($sm) {
			padding: 30px 0;
		}
		p {
			font-size: rem(24) !important;
			color: #4d4d4d;
			@include breakpoint($sm) {
				font-size: rem(18) !important;
			}
			span {
				animation: dotOpacity 0.8s linear;
				animation-iteration-count: infinite;
				&:first-of-type {
					animation-delay: 1s;
				}
				&:nth-of-type(2) {
					animation-delay: 0.5s;
				}
				&:nth-of-type(3) {
					animation-delay: 0s;
				}
			}
		}
	}
}
@keyframes dotOpacity {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}